import React, { Component } from 'react'
import { Link } from 'react-router'
import { connect } from 'react-redux'
import { wrap } from 'react-bounds'
import ReactCSS from 'reactcss'
import sty from '../../styles/home'
import { getBounds } from '../../styles/bounds'
import content from '../../assets/content.json'


class Home extends React.Component{

  static bounds() {
    return {
        'largeScreen': {
            minWidth: 1201
        },
        'middleScreen': {
            minWidth: 901,
            maxwidth: 1200
        },
        'smallScreen': {
            minWidth: 601,
            maxWidth: 900
        },
        'mobile': {
            minWidth: 301,
            maxWidth: 600
        }
    }
  }

  render() {
    let cont = content[this.props.language].home
    var styles = ReactCSS({
          'default': {
            body: {
              background: 'white'
            },
          },
          'middleScreen':{
            body: {
              background: 'green'
            }
          },
          'mobile': {
            body: {
              background: 'red'
            },
          },
    })
    return (
      <div style={styles.body}>
          <p style={sty.title}>
            {cont.title}
          </p>
          <div style={sty.content}>
            &emsp;&emsp;{cont.p1} <br/>
            &emsp;&emsp;{cont.p2}
            &nbsp;<Link style={sty.link} to="/projects">{cont.p3}</Link>&nbsp;
           {cont.p4}
          </div>
      </div>
    )
  }
}

const mapStateToProps = (state,prop)=>{
    return {
      language: state.common.language,
    }
}

const mapDispatchToProps = {
    //switLan : (lan) => navAction.switLan(lan),
    //switNavItem: (item) => navAction.switNavItem(item)
    //getUsrInf : comAction.getUsrInf()
}

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(wrap(Home))

